        const { currency, razdely, lvl } = props.app;
        const { authenticated } = props.user;
        const { setData } = props.appActions;
        let [kategory, setKategory] = useState([]);
        let [selectKategory, setSelectKategory] = useState('');
        let [podkategory, setPodKategory] = useState([]);
        let [show, setShow] = useState(false);
        useEffect(async ()=>{
            let data = await getOther({name: 'Категория'})
            await setKategory(data)
          },[]);
